.app {
  display: flex;
  flex-direction: column;
  align-items: center;
}

button {
  background-color: lightgreen;
  border: 1px solid green;
  border-radius: 3px;
  font-size: 20px;
  padding: 10px;
  width: 30%;
}
.animal-list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.animal-show {
  position: relative;
  border: 1px solid lightgrey;
  padding: 10px;
  border-radius: 5px;
  margin: 10px;
  box-shadow: 0px 3px 4px lightgray;
}

.animal {
  height: 200px;
}
.heart {
  position: absolute;
  bottom: 10%;
  right: 10%;
}

